<template>
  <div>
    <div class='bread'>
      <strong></strong>
      <span style="margin-left: 50px">
           : <el-switch
        :value="$dc.ruleState"
        @input="$dc.setRuleState"
        on-color="#13ce66"
        off-color="#ff4949">
              </el-switch>
      </span>
    </div>
    <el-row :gutter="20" style="margin-bottom: 10px">
      <el-col :span="10" :offset="14" class="addrule-btn-wrap">
        <input type="file" @change="fileUpload" class="importfile"/>
        <el-button size="small"></el-button>
        <el-button size="small" type="primary" @click='importRemoteRule'></el-button>
        <el-button size="small" type="primary" @click='addRuleCollection'></el-button>
      </el-col>
    </el-row>
    <el-table border align='center' :data="$dc.ruleFileList" :row-class-name="rowClassName">
      <el-table-column prop="checked" label="" align="center" width="70">
        <template scope='scope'>
          <el-checkbox v-model="scope.row.checked" :disabled="!$dc.ruleState"
                       @change="onSelectionChange(scope.row.name,scope.row.checked)"></el-checkbox>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="" align="center" width="150" >
        <template scope='scope'>
          {{scope.row.name}}
        </template>
      </el-table-column>
      <el-table-column label="	" align="center" width="70" >
        <template scope='scope'>
          {{scope.row.meta.remote ? '' : '-' }}
        </template>
      </el-table-column>
      <el-table-column prop="description" label="" align="center">
      </el-table-column>

      <el-table-column label="" :width="200" align="center" :context="_self">
        <template scope='scope'>
          <a :href="'#/editrule?name='+scope.row.name">
            <el-tooltip class="item" effect="dark" content="" placement="top-start">
              <el-button type="info" icon='edit' size="mini">
              </el-button>
            </el-tooltip>
          </a>
          <el-tooltip class="item" effect="dark" content="" placement="top-start">
            <el-button type="info" icon='share' size="mini"
                       @click='onShareFile(scope.row,scope.$index)'>
            </el-button>
          </el-tooltip>
          <span>
            <el-button type="danger" icon='delete' size="mini" @click='onDeleteFile(scope.row,scope.$index)'>
            </el-button>
          </span>
          <span>
            <el-tooltip class="item" effect="dark" content="" placement="top-start">
              <el-button type="success" icon='information' size="mini"
                         v-if="scope.row.meta.remote && scope.row.meta.remoteETag && (scope.row.meta.ETag !== scope.row.meta.remoteETag)"
                         @click='onUpdateFile(scope.row,scope.$index)'>
              </el-button>
            </el-tooltip>
          </span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
  import ruleApi from '../../../api/rule'
  import _ from 'lodash'
  export default {
    name: 'rulefilelist',
    methods: {
      rowClassName(row) {
        if (row.meta.remote) {
          return 'remote-rule';
        }
        return '';
      },
      onDeleteFile(row, index) {
        this.$confirm(`: ${row.name}, ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          ruleApi.deleteFile(row.name).then((response) => {
            var serverData = response.data;
            if (serverData.code == 0) {
              this.$message({
                type: 'success',
                message: '!'
              });
            } else {
              this.$message.error(`${serverData.msg}`);
            }
          });
        })
      },
      onShareFile(row, index) {
        if (!row.meta.remote) {
          window.location = '/rule/download?name=' + row.name;
        } else {
          this.$alert(row.meta.url, 'URL', {
            confirmButtonText: 'OK',
            callback: action => {
            }
          });
        }
      },
      onUpdateFile(row, index) {
        this.$confirm(`: ${row.name} ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          ruleApi.getRemoteRuleFile(row.meta.url).then((response) => {
            var remoteFileResponse = response.data;
            var content = remoteFileResponse.data;
            content.meta = {
              "remote": true,
              "url": row.meta.url,
              "ETag": remoteFileResponse.headers['etag'],
              "remoteETag": remoteFileResponse.headers['etag']
            };
            content.name = content.name + '-remote';
            ruleApi.saveFile(content.name, content).then((response) => {
              var serverData = response.data;
              if (serverData.code == 0) {
                // 
                this.$message({
                  message: '',
                  type: 'success'
                });
              } else {
                _this.$message.error(`${serverData.msg}`);
              }
            });
          })
        })
      },
      importRemoteRule(){
        this.$prompt('url', '', {
          confirmButtonText: '',
          cancelButtonText: '',
        }).then((result) => {
          var url = result.value;
          console.log(url)
          ruleApi.getRemoteRuleFile(url).then((response) => {
            var remoteFileResponse = response.data;
            var content = remoteFileResponse.data;
            content.meta = {
              "remote": true,
              "url": url,
              "ETag": remoteFileResponse.headers['etag'],
              "remoteETag": remoteFileResponse.headers['etag']
            };
            content.name = content.name + '-remote';
            // 
            // 
            var varNameList = ruleApi.getReferenceVar(content);
            var infoStr;
            if (varNameList.length > 0) {
              infoStr = `${content.name},${varNameList.join('; ') }`;
            } else {
              infoStr = `${content.name}`;
            }
            this.$alert(infoStr, '', {
              confirmButtonText: '',
              callback: action => {
                if (action == 'confirm') {
                  // 
                  ruleApi.saveFile(content.name , content).then((response) => {
                    var serverData = response.data;
                    if (serverData.code == 0) {
                      // 
                      this.$message({
                        message: '',
                        type: 'success'
                      });
                    } else {
                      this.$message.error(`${serverData.msg}`);
                    }
                  });
                }
              }
            });
          })
        })
      },
      onSelectionChange(name, checked) {
        ruleApi.setFileCheckStatus(name, checked).then((response) => {
          var serverData = response.data;
          if (serverData.code == 0) {
            this.$message({
              type: 'success',
              message: '!'
            });
          } else {
            this.$message.error(`${serverData.msg}`);
          }
        });
      },
      addRuleCollection(){
        this.$router.push('createrulefile');
      },
      fileUpload(evt){
        var _this = this;
        var files = evt.target.files;
        var file = files[0];
        if (!file || file.type.indexOf('json') < 0)return;


        var reader = new FileReader();
        reader.onload = function (e) {
          var contentStr = e.target.result;
          var content = JSON.parse(contentStr);

          // 
          var finded = _.find(_this.fileList, (file) => {
            return file.name == content.name;
          });
          if (finded) {
            _this.$message.error(`${content.name}name,`);
            return;
          }
          // 
          var varNameList = ruleApi.getReferenceVar(content);
          var infoStr;
          if (varNameList.length > 0) {
            infoStr = `${content.name},${varNameList.join('; ') }`;
          } else {
            infoStr = `${content.name}`;
          }
          _this.$alert(infoStr, '', {
            confirmButtonText: '',
            callback: action => {
              if (action == 'confirm') {
                // 
                ruleApi.saveFile(content.name, content).then((response) => {
                  var serverData = response.data;
                  if (serverData.code == 0) {
                    // 
                    _this.$message({
                      message: '',
                      type: 'success'
                    });
                  } else {
                    _this.$message.error(`${serverData.msg}`);
                  }
                });
              }
            }
          });
        };
        reader.readAsText(file);
      }
    }
  }

</script>
<style>
  .addrule-btn-wrap {
    text-align: right;
  }

  .importfile {
    position: absolute;
    width: 80px;
    height: 28px;
    opacity: 0;
    cursor: pointer;
  }
  .el-table .remote-rule {
    background-color: #eee;
  }

  .importfile::-webkit-file-upload-button {
    cursor: pointer;
  }
</style>



