<template>
  <div style="text-align: left">
    <!--  -->
    <div class="el-form demo-form-inline el-form--inline conditon">
      <div class="el-form-item" style="margin-bottom:8px;">
        <label class="el-form-item__label"></label>
        <div class="el-form-item__content">
          <el-select v-model="rule.method" style="width: 80px;" size="small" placeholder="" :disabled="remote">
            <el-option v-for="item in methodlist" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="el-form-item"
           style="margin-bottom:8px;width: calc(100% - 140px);padding-left: 20px;margin-right: 0px">
        <div class="el-form-item__content" style="width: 100%">
          <el-input v-model="rule.match" style="width: 100%" size="small" :disabled="remote"
                    placeholder="urlurl"></el-input>
        </div>
      </div>
    </div>
    <div style="padding: 10px 0;" >
      <span style="width: 85%;display: inline-block">
        <div>
          <el-input v-model="rule.name" size="small" placeholder=""></el-input>
        </div>
      </span>
      <span style="width: 10%;display: inline-block">
        <el-button type="text" @click="addAction" :disabled="remote"></el-button>
      </span>
    </div>
    <div>
      <div v-for="action,index in rule.actionList" :key="index" class="dashed-border">
        <span style="width: 85%;display: inline-block">
          <action-detail :action="action"
                         :remote="remote"></action-detail>
        </span>
        <span style="width: 10%;display: inline-block;vertical-align: bottom;line-height: 107px;height: 107px;">
          <el-button type="text" :disabled="remote" @click="deleteAction(index)"></el-button>
        </span>
      </div>

    </div>
  </div>
</template>

<script>
  import _ from 'lodash'
  import Vue from 'vue';
  import ActionDetail from './ActionDetail.vue'
  Vue.component(ActionDetail.name, ActionDetail);
  export default {
    name: 'rule-detail',
    props: ['rule', 'remote'],
    data() {
      return {
        methodlist: [
          {value: '', label: ''},
          {value: 'get', label: 'GET'},
          {value: 'post', label: 'POST'},
          {value: 'put', label: 'PUT'},
          {value: 'patch', label: 'PATCH'},
          {value: 'delete', label: 'DELETE'}
        ]
      }
    },
    methods: {
      addDataFile(){
        // 
        this.$dc.requestAddDataFile((id) => {
          this.rule.action.data.data_id = id;
        });
      },
      editDataFile(entry){
        this.$dc.requestEditDataFile(entry);
      },
      addAction(){
        this.rule.actionList.push({
          type: "redirect",// redirect
          data: {
            target: "",// 
            dataId: '', //id
            modifyResponseBody: '',// body
            modifyResponseHeader: '',// header
            cookie: "", // cookie
            callbackName: "" // jsonp
          }
        });
      },
      deleteAction(index){
        this.rule.actionList.splice(index, 1);
      }
    },
    computed: {
      datafileEntry(){
        if (this.rule.action.type == "data") {
          var finded = _.find(this.$dc.dataList, (entry) => {
            return entry.id == this.rule.action.data.data_id;
          });
          if (!finded) this.rule.action.data.data_id = '';
          return finded;
        }
      }
    }
  }

</script>
<style>
  .inline-block {
    display: inline-block;
  }

  .conditon {
    margin-top: 10px;
  }

  .conditon:after {
    content: "";
    display: block;
    border-bottom: 1px dotted #adadad;
  }

  .dashed-border{
    border-top: 1px dotted #adadad;
  }

  .action {
    margin-bottom: 10px;
  }

  .left-panel {
    margin-top: 10px;
    margin-bottom: 10px;
    height: 80px;
    width: 140px;
    vertical-align: top;
    border-right: 1px dotted #c0c0c0;
  }

  .right-panel {
    width: calc(100% - 150px);
    height: 80px;
    padding-left: 10px;
  }

  .action-data {
    text-align: left;
    margin-top: 35px;

  }

  .action-option {
    margin-top: 5px;
  }
</style>



