<template>
    <div>
        <div class='bread'>
            <strong>Host{{loaded ? ': '+filecontent.name: ''}}</strong>
        </div>
        <el-row :gutter="20" style="margin-bottom: 10px">
            <el-col :span="6" :offset="18">
                <el-button size="small" @click='addRow'>Host Entry</el-button>
                <el-button size="small" type="primary" @click='saveFile'></el-button>
            </el-col>
        </el-row>
        <el-table border style="width: 100%" align='center' :data="hostarray">
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="key" label="" align="center">
                <template scope='scope'>
                    <el-input v-model="scope.row.key" size="small" placeholder=""></el-input>
                </template>
            </el-table-column>
            <el-table-column prop="value" label="ip" align="center" width="400">
                <template scope='scope'>
                    <el-input v-model="scope.row.value" size="small" placeholder=""></el-input>
                </template>
            </el-table-column>
            <el-table-column label="" :width="80" align="center" :context="_self">
                <template scope='scope'>
                    <el-button type="danger" icon='delete' size="mini"
                               @click='onDeleteRow(scope.row,scope.$index,filecontent.content)'>
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import hostApi from '../../../api/host';
    import forEach from 'lodash/forEach';
    export default {
        name: 'app',
        data() {
            return {
                loaded: false,
                filecontent: {},
                hostarray: []
            }
        },
        methods: {
            getFile() {
                this.loaded = false;
                hostApi.getFileContent(this.$route.query.name).then((response) => {
                    var serverData = response.data;
                    if (serverData.code == 0) {
                        this.loaded = true;
                        this.filecontent = serverData.data;
                        // host
                        this.hostarray = [];
                        forEach(this.filecontent.content, (value, key) => {
                            this.hostarray.push({
                                key: key,
                                value: value
                            })
                        });
                    } else {
                        this.$message.error(`${serverData.msg}`);
                    }
                });
            },
            onDeleteRow(row, index, list) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.hostarray.splice(index, 1);
                })
            },
            saveFile() {
                // host
                var content = {};
                forEach(this.hostarray, (obj) => {
                    content[obj.key] = obj.value;
                });
                this.filecontent.content = content;

                hostApi.saveFile(this.$route.query.name, this.filecontent).then((response) => {
                    var serverData = response.data;
                    if (serverData.code == 0) {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    } else {
                        this.$message.error(`${serverData.msg}`);
                    }
                });
            },
            addRow() {
                this.hostarray.unshift({
                    key: "",
                    value: ""
                })
            }
        },
        mounted() {
            this.getFile();
        },
        watch: {
            '$route'(to, from) {
                this.getFile();
            }
        }
    }

</script>



